const userInfo = new UserInfo({ 
  userName: '.profile__name', 
  workName: '.profile__description',
  avatar: '.profile__avatar'
} );

  // 
let userId
api.getUserId()
.then((userData) => {
  userInfo.setUserInfo(userData)
  userId = userData._id
})
.catch(err => {
console.log(`  . ${err}`)
})

