// 
 const createCard = (cardData) => {
  const card = new Card(cardData.name, cardData.link, handleCardClick, '.img-template')
  const cardElement = card.prepareCard();
  return cardElement;
 }

//  
 const section = new Section({ 
  renderer: (card) => {
    section.addItem(createCard(card))} 
},
container);


